.btn-outline-primary, .wpcf7 input[type=submit] {
  color: var(--col-pink-400);
  border-color: var(--col-pink-400);
}
.btn-outline-primary:hover, .wpcf7 input[type=submit]:hover {
  color: #fff;
  background-color: var(--col-pink-400);
  border-color: var(--col-pink-400);
}
.btn-outline-primary:disabled, .wpcf7 input[type=submit]:disabled {
  color: var(--col-pink-400);
}

.btn-primary {
  color: #fff;
  background-color: var(--col-pink-400);
  border-color: var(--col-pink-400);
}
.btn-primary:hover {
  background-color: color-mix(in srgb, var(--col-pink-400), #fff 20%);
  border-color: color-mix(in srgb, var(--col-pink-400), #fff 20%);
}

@keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(0deg);
  }
  10% {
    transform: translate(-1px, -2px) rotate(-1deg);
  }
  20% {
    transform: translate(-3px, 0px) rotate(1deg);
  }
  30% {
    transform: translate(3px, 2px) rotate(0deg);
  }
  40% {
    transform: translate(1px, -1px) rotate(1deg);
  }
  50% {
    transform: translate(-1px, 2px) rotate(-1deg);
  }
  60% {
    transform: translate(-3px, 1px) rotate(0deg);
  }
  70% {
    transform: translate(3px, 1px) rotate(-1deg);
  }
  80% {
    transform: translate(-1px, -1px) rotate(1deg);
  }
  90% {
    transform: translate(1px, 2px) rotate(0deg);
  }
  100% {
    transform: translate(1px, -2px) rotate(-1deg);
  }
}
@keyframes pop {
  from {
    opacity: 1;
    transform: translateZ(0) scale(1, 1);
  }
  to {
    opacity: 0;
    transform: translateZ(0) scale(1.75, 1.75);
  }
}
@keyframes residue {
  from {
    opacity: 0;
    color: #fff;
    transform: scale(1, 1);
  }
  to {
    opacity: 1;
    color: #000;
    transform: scale(1, 1);
  }
}
.pop:hover {
  animation: shake 250ms linear forwards;
}
.pop:hover .bubble--bubble {
  animation: pop 500ms cubic-bezier(0.16, 0.87, 0.48, 0.99) 250ms forwards;
}
.pop:hover .bubble--inner {
  animation: residue 500ms cubic-bezier(0.16, 0.87, 0.48, 0.99) 250ms forwards;
}

.bubble {
  position: relative;
  display: grid;
  place-content: center;
  min-width: 80px;
  max-width: 4ch;
  aspect-ratio: 1;
  color: black;
  cursor: pointer;
  text-decoration: none;
  text-align: center;
  isolation: isolate;
  border-radius: 50%;
  box-shadow: inset 0 0 10px hsla(var(--hsl-aqua-400)/0.8);
}
.bubble--bubble {
  position: absolute;
  inset: 0;
  aspect-ratio: 1;
  width: 100%;
  border-radius: 100vw;
  background-color: var(--col-pink-400);
}
.bubble--inner {
  z-index: 9;
  color: white;
  font-family: "Cafeteria";
  font-weight: 900;
  font-size: 1.4rem;
  line-height: 1;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  word-spacing: 100vw;
}

.grid {
  --_gap: 1rem;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
}
.grid__card {
  text-decoration: none;
  box-sizing: border-box;
  margin: 0.5rem;
  flex: 1 1 calc(100% - 1rem);
  /* Each card takes up 100% of the container's width for very small screens */
}
@media (min-width: 768px) {
  .grid__card {
    flex: 1 1 calc(50% - 1rem);
    /* Each card takes up 1/2 of the container's width for smaller screens */
    max-width: calc(50% - var(--_gap));
  }
}
@media (min-width: 992px) {
  .grid__card {
    flex: 1 1 calc(33.333% - var(--_gap));
    min-width: calc(33.333% - var(--_gap));
    max-width: calc(33.333% - var(--_gap));
  }
}
.grid .card {
  display: grid;
  grid-template-rows: auto 1fr;
  align-items: stretch;
  color: var(--col-dark);
  background-color: var(--col-light);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-lg);
  overflow: hidden;
  height: 100%;
  transition: box-shadow var(--transition), transform var(--transition);
}
.grid .card:hover {
  transform: translateY(2px);
  box-shadow: var(--shadow-sm);
}
.grid .card__image_container {
  position: relative;
  overflow: hidden;
  isolation: isolate;
}
.grid .card__flash {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  border-radius: var(--border-radius-sm);
  font-size: var(--fs-200);
  text-transform: uppercase;
  background-color: var(--col-pink-400);
  padding: 0.25em 1em;
  color: var(--col-light);
  z-index: 9;
}
.grid .card__image {
  aspect-ratio: 16/9;
  width: 100%;
  object-fit: cover;
  transition: scale var(--transition);
}
.grid .card__inner {
  padding: 1rem;
  display: grid;
  grid-template-rows: auto 1rem 1fr;
  gap: 0.5rem;
}
.grid .card__date {
  font-size: var(--fs-200);
}
.grid .card__title {
  font-size: var(--fs-600);
}
.grid .grid__card--sm .card__inner {
  padding: 1rem;
  display: grid;
  grid-template-rows: auto;
}

.single-blog {
  padding-top: calc(var(--h-top) + 2rem);
}
.single-blog h2, .single-blog .h2 {
