.text_image__container {
  background-color: var(--col-light);
  padding: 1rem 1rem 5rem 1rem;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  max-width: 460px;
  margin-inline: auto;
}
.text_image__img-left {
  transform: rotate(-2deg);
}
.text_image__img-right {
  transform: rotate(3deg);
}
.text_image__image {
  aspect-ratio: 4/3;
  object-fit: cover;
  object-position: center;
}

.products-latest__grid {
  display: flex;
  gap: 2rem;
  justify-content: center;
  flex-wrap: wrap;
}
.products-latest__grid .slick-slide {
  padding-block: 2rem 3rem;
}
.products-latest__product {
  display: block;
  background-color: var(--col-light);
  padding: 1rem;
  text-decoration: none;
  max-width: calc(300px + 2rem);
  margin-inline: auto;
  transition: transform var(--transition-short), box-shadow var(--transition-short);
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}
.products-latest__product img {
  margin-bottom: 1rem;
}
.products-latest__product h3, .products-latest__product .h3 {
  text-align: center;
  color: var(--col-dark);
  font-size: var(--fs-500);
}
.products-latest__product.odd:hover, .products-latest__product.even:hover {
  transform: rotate(0) scale(1);
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}
.products-latest__product.even {
  transform: rotate(-2deg) scale(0.9);
}
.products-latest__product.odd {
  transform: rotate(3deg) scale(0.9);
}

.testimonials__inner {
